import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Select, DatePicker, Input } from 'antd';
import { inOutStudent } from 'dump/data';

const { Option } = Select;
const { TextArea } = Input;

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    studentCode: '',
    name: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formTeacher' })
class FormInStudent extends Component {
  state = {
    teacherId: true,
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ teacherId: match.params.id });
      this.getTeacherById(match.params.id);
    }
  };

  getTeacherById = studentCode => {
    const teacherMap = inOutStudent.filter(teacher => teacher.studentCode === studentCode)[0];
    const initialValue = handleInitialValue(teacherMap);
    console.log(initialValue);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { teacherId } = this.state;
    const config = {
      rules: [{ type: 'object', message: 'Please select time!' }],
    };
    return (
      <Card title={teacherId ? 'Nội trú' : 'Nội trú'}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item hasFeedback label="Mã Sinh Viên" {...formItemLayout}>
              {getFieldDecorator('studentCode', {
                initialValue: initialValue.studentCode,
                rules: [
                  {
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input disabled />)}
            </Form.Item>
            <Form.Item hasFeedback label="Họ và Tên" {...formItemLayout}>
              {getFieldDecorator('name', {
                initialValue: initialValue.name,
                rules: [
                  {
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input disabled />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Từ ngày">
              {getFieldDecorator('date-picker', config)(<DatePicker />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tòa nhà" hasFeedback>
              {getFieldDecorator('aaa', {
                // initialValue: initialValue.name,
                rules: [
                  {
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select defaultValue="lucy">
                  <Option value="OK">OK</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tầng" hasFeedback>
              {getFieldDecorator('bbb', {
                // initialValue: initialValue.name,
                rules: [
                  {
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select defaultValue="lucy">
                  <Option value="OK">OK</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Số phòng" hasFeedback>
              {getFieldDecorator('ccc', {
                // initialValue: initialValue.name,
                rules: [
                  {
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select defaultValue="lucy">
                  <Option value="OK">OK</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Ghi chú" hasFeedback>
              {getFieldDecorator('ddd', {
                // initialValue: initialValue.name,
                rules: [
                  {
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<TextArea rows={8} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel href="#in-out-student/list" />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormInStudent {...props} />
  </Page>
));



